body, input[type="text"], input[type="password"], textarea, pre {
    background-color: #222;
    color: #fff;
    margin: 0;
    padding: 3px;
}

.header {
    position: -webkit-sticky; /* Safari向けの設定 */
    position: sticky;
    top: 0;
    background-color: #222;
}

table {
    width: 100%;
    max-width: 100%; /* 任意: テーブルの幅が親要素を超えないように設定 */
    border-collapse: collapse; /* セルのボーダーが重なるように設定 */
}

pre {
    white-space: pre-wrap;      /* CSS3 */
    white-space: -moz-pre-wrap; /* Firefox */
    white-space: -pre-wrap;     /* Opera <7 */
    white-space: -o-pre-wrap;   /* Opera 7 */
    word-wrap: break-word;      /* IE 5.5+ */
    font-family: Arial, sans-serif;
}

.collapse-content {
    display: none;
}

svg { /* for ABC.js dark mode */
    fill: currentColor;
}
svg path {
    fill: currentColor;
}

html, body {
    height: 100%;
    margin: 0;
    padding: 0;
}
